import { AxiosInstance } from 'axios';
import { AxiosParam, create, send } from 'summer';
import { Cfop } from '../models/dtos/ger/cfop';
import { Especie } from '../models/dtos/ger/especie';
import { ConfigLivroMLF } from '../models/dtos/mlf/configLivroMLF';
import { ConfigReinf } from '../models/dtos/mlf/configReinf';
import { ImportarIntegracaoFiscalContabilParam } from '../models/dtos/mlf/importarIntegracaoFiscalContabilParam';
import { RefazerLancamentosContabeisParam } from '../models/dtos/mlf/refazerLancamentosContabeisParam';
import { RefazerLancamentosReinfParam } from '../models/dtos/mlf/refazerLancamentosReinfParam';

function getService(): AxiosInstance {
  return create('/mlf');
}

export function findEspecieByModelo(modelo: string, isImportacao: boolean, param: AxiosParam<Especie>) {
  send(
    getService().post<Especie>('/findEspecieByModelo', { modelo, isImportacao }),
    param
  );
}

export function findReinfCFOPConfig(idCFOP: number, param: AxiosParam<Cfop>) {
  send(getService().get<Cfop>(`/findReinfCFOPConfig/${idCFOP}`), param);
}

export function getLivroFolhaMLF(idGerEmpresa: number, param: AxiosParam<ConfigLivroMLF>) {
  send(getService().get<ConfigLivroMLF>(`/getLivroFolhaMLF/${idGerEmpresa}`), param);
}

export function refazerLancamentosContabeis(refazerLancamentosContabeisParam: RefazerLancamentosContabeisParam, param: AxiosParam<any>) {
  send(getService().post<RefazerLancamentosContabeisParam>('/refazerLancamentosContabeis', refazerLancamentosContabeisParam), param);
}

export function refazerLancamentosReinf(refazerLancamentosReinfParam: RefazerLancamentosReinfParam, param: AxiosParam<any>) {
  send(getService().post<RefazerLancamentosReinfParam>('/refazerLancamentosReinf', refazerLancamentosReinfParam), param);
}

export function findConfigReinfEmpresa(idEmpresaMlf: number, param: AxiosParam<any>) {
  send(getService().get<ConfigReinf>(`/findConfigReinfEmpresa/${idEmpresaMlf}`), param);
}

export function importarIntegracaoFiscalContabil(importarParam: ImportarIntegracaoFiscalContabilParam, param: AxiosParam<any>) {
  send(getService().post<any>('/importarIntegracaoFiscalContabil', importarParam), param);
}

export function validarIE(inscricaoEstadual: string, unidadeFederacao: string, param: AxiosParam<boolean>) {
  send(
    getService().post<boolean>('/validarIE', { inscricaoEstadual, unidadeFederacao }),
    param
  );
}
