import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardDeck, Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, ERROR, getSelectedItem, InputGroupInline, InputType, isEmpty, showToast, WindowComponent } from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { CLASS_NAME, ConfigLivroMLF } from '../../../../models/dtos/mlf/configLivroMLF';
import { getLivroFolhaMLF } from '../../../../services/mlf.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface ConfigLivroFolhaMFLEditViewProps extends EditViewProps<ConfigLivroMLF> {}

const ConfigLivroFolhaMFLEditView: FC<ConfigLivroFolhaMFLEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const { reset, ...formProps } = useForm<ConfigLivroMLF>({ defaultValues: selectedItem, mode: 'onBlur' });
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  useEffect(
    () => {
      reset({
        apuracaoIcmsFolha: 0,
        apuracaoIcmsLivro: '0',
        apuracaoIpiFolha: 0,
        apuracaoIpiLivro: '0',
        ciapFolha: 0,
        ciapLivro: '0',
        entradaFolha: 0,
        entradaLivro: '0',
        inventarioFolha: 0,
        inventarioLivro: '0',
        prestacaoServicoFolha: 0,
        prestacaoServicoLivro: '0',
        producaoEstoqueFolha: 0,
        producaoEstoqueLivro: '0',
        saidaFolha: 0,
        saidaLivro: '0',
      });
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  useEffect(
    () => {
      const { empresa } = globalReducer.globalParameter;
      if (!isEmpty(empresa)) {
        getLivroFolhaMLF(empresa.id, {
          errorFunction: mensagem => {
            showToast(mensagem, ERROR);
          },
          thenFunction: result => {
            if (result) {
              reset(result);
            } else {
              setTimeout(() => formProps.setValue('idGerEmpresa', empresa.id), 800);
            }
          },
        });
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const validateExtras = (formData: ConfigLivroMLF): string[] => {
    const errors = [];

    if (!isEmpty(formData.entradaLivro) && Number(formData.entradaFolha) < 2) {
      errors.push('Livro de Entrada - Página Inicial deve ser maior que 1.');
    }

    if (!isEmpty(formData.saidaLivro) && Number(formData.saidaFolha) < 2) {
      errors.push('Livro de Saída - Página Inicial deve ser maior que 1.');
    }

    if (!isEmpty(formData.apuracaoIcmsLivro) && Number(formData.apuracaoIcmsFolha) < 2) {
      errors.push('Livro de Apuração do ICMS - Página Inicial deve ser maior que 1.');
    }

    if (!isEmpty(formData.apuracaoIpiLivro) && Number(formData.apuracaoIpiFolha) < 2) {
      errors.push('Livro de Apuração do IPI - Página Inicial deve ser maior que 1.');
    }

    if (!isEmpty(formData.prestacaoServicoLivro) && Number(formData.prestacaoServicoFolha) < 2) {
      errors.push('Livro de Prestação de Serviço - Página Inicial deve ser maior que 1.');
    }

    if (!isEmpty(formData.inventarioLivro) && Number(formData.inventarioFolha) < 2) {
      errors.push('Livro Inventário - Página Inicial deve ser maior que 1.');
    }

    if (!isEmpty(formData.producaoEstoqueLivro) && Number(formData.producaoEstoqueFolha) < 2) {
      errors.push('Livro Controle Produção e Estoque - Página Inicial deve ser maior que 1.');
    }

    if (!isEmpty(formData.ciapLivro) && Number(formData.ciapFolha) < 2) {
      errors.push('Livro CIAP - Página Inicial deve ser maior que 1.');
    }

    return errors;
  };

  return (
    <WindowComponent
      title="Manutenção de Livros Fiscais"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter)]}
    >
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras}>
        <InputGroupInline formProps={formProps} id="idGerEmpresa" hidden={true} />
        <CardDeck className="mb-4">
          <Card style={{ marginBottom: '16px', borderWidth: 'thin' }}>
            <CardBody>
              <Row>
                <Col />
                <Col className="font-weight-bold">Nº Livro</Col>
                <Col className="font-weight-bold">Página Inicial</Col>
              </Row>
              <Row>
                <Col>Registro de Entrada</Col>
                <Col>
                  <InputGroupInline
                    formProps={{ ...formProps, validation: { required: true, maxLength: 7 } }}
                    hideLabel={true}
                    id="entradaLivro"
                    inputProps={{ autoFocus: true }}
                  />
                </Col>
                <Col>
                  <InputGroupInline
                    formProps={{ ...formProps, validation: { required: true, maxLength: 6 } }}
                    hideLabel={true}
                    id="entradaFolha"
                    type={InputType.NUMBER}
                  />
                </Col>
              </Row>
              <Row>
                <Col>Registro de Saída</Col>
                <Col>
                  <InputGroupInline formProps={{ ...formProps, validation: { required: true, maxLength: 7 } }} hideLabel={true} id="saidaLivro" />
                </Col>
                <Col>
                  <InputGroupInline
                    formProps={{ ...formProps, validation: { required: true, maxLength: 6 } }}
                    hideLabel={true}
                    id="saidaFolha"
                    type={InputType.NUMBER}
                  />
                </Col>
              </Row>
              <Row>
                <Col>Apuração de ICMS</Col>
                <Col>
                  <InputGroupInline
                    formProps={{ ...formProps, validation: { required: true, maxLength: 7 } }}
                    hideLabel={true}
                    id="apuracaoIcmsLivro"
                  />
                </Col>
                <Col>
                  <InputGroupInline
                    formProps={{ ...formProps, validation: { required: true, maxLength: 6 } }}
                    hideLabel={true}
                    id="apuracaoIcmsFolha"
                    type={InputType.NUMBER}
                  />
                </Col>
              </Row>
              <Row>
                <Col>Apuração de IPI</Col>
                <Col>
                  <InputGroupInline
                    formProps={{ ...formProps, validation: { required: true, maxLength: 7 } }}
                    hideLabel={true}
                    id="apuracaoIpiLivro"
                  />
                </Col>
                <Col>
                  <InputGroupInline
                    formProps={{ ...formProps, validation: { required: true, maxLength: 6 } }}
                    hideLabel={true}
                    id="apuracaoIpiFolha"
                    type={InputType.NUMBER}
                  />
                </Col>
              </Row>
              <Row>
                <Col>Prestação de Serviço</Col>
                <Col>
                  <InputGroupInline
                    formProps={{ ...formProps, validation: { required: true, maxLength: 7 } }}
                    hideLabel={true}
                    id="prestacaoServicoLivro"
                  />
                </Col>
                <Col>
                  <InputGroupInline
                    formProps={{ ...formProps, validation: { required: true, maxLength: 6 } }}
                    hideLabel={true}
                    id="prestacaoServicoFolha"
                    type={InputType.NUMBER}
                  />
                </Col>
              </Row>
              <Row>
                <Col>Inventário</Col>
                <Col>
                  <InputGroupInline
                    formProps={{ ...formProps, validation: { required: true, maxLength: 7 } }}
                    hideLabel={true}
                    id="inventarioLivro"
                  />
                </Col>
                <Col>
                  <InputGroupInline
                    formProps={{ ...formProps, validation: { required: true, maxLength: 6 } }}
                    hideLabel={true}
                    id="inventarioFolha"
                    type={InputType.NUMBER}
                  />
                </Col>
              </Row>
              <Row>
                <Col>Controle de Produção e Estoque</Col>
                <Col>
                  <InputGroupInline
                    formProps={{ ...formProps, validation: { required: true, maxLength: 7 } }}
                    hideLabel={true}
                    id="producaoEstoqueLivro"
                  />
                </Col>
                <Col>
                  <InputGroupInline
                    formProps={{ ...formProps, validation: { required: true, maxLength: 6 } }}
                    hideLabel={true}
                    id="producaoEstoqueFolha"
                    type={InputType.NUMBER}
                  />
                </Col>
              </Row>
              <Row>
                <Col>CIAP</Col>
                <Col>
                  <InputGroupInline formProps={{ ...formProps, validation: { required: true, maxLength: 7 } }} hideLabel={true} id="ciapLivro" />
                </Col>
                <Col>
                  <InputGroupInline
                    formProps={{ ...formProps, validation: { required: true, maxLength: 6 } }}
                    hideLabel={true}
                    id="ciapFolha"
                    type={InputType.NUMBER}
                  />
                </Col>
              </Row>
            </CardBody>
          </Card>
        </CardDeck>
      </EditForm>
    </WindowComponent>
  );
};

export default ConfigLivroFolhaMFLEditView;
